import * as Yup from 'yup';

const LoginSchema = Yup.object().shape({
    email: Yup.string()
        .min(3, '    8 ')
        .max(30, '  30 ')
        .required('   '),
    password: Yup.string()
        .min(8, '    8 ')
        .max(30, '  30 ')
        .required('   '),
});

export default LoginSchema;